import type { Recursive } from './array-flatten.js';
import { arrayFlatten } from './array-flatten.js';

/**
 * @description
 * Recursively flattens array.
 *
 * @example
 * ``` ts
 * arrayFlatten([1, 2, [3, 4, [5, 6]]]);
 * // => [ 1, 2, 3, 4, 5, 6 ]
 * ```
 *
 * @param arr The array to flatten.
 * @returns Returns the new flattened array.
 *
 * @docsCategory preference/utils/array
 * @codeFilter array-flatten-deep
 */
export function arrayFlattenDeep<T>(arr: Recursive<T>): Array<T> {
  return arrayFlatten(arr, Infinity) as Array<T>;
}
